const Category = (n,o) => {
    o.forEach(v => {
        if(v.CategoryItemId == n){
            n = v.CategoryItemName
        }
    })
    return n
}
const CategoryId = (n,o) => {
    o.forEach(v => {
        if(v.CategoryItemValue == n){
            n = v.CategoryItemName
        }
    })
    return n
}
const formatTime = n => {
    return n.slice(0,5)
}
const PackStatus = n => {
    n = Number(n)
    switch(n){
        case 0: 
            n = '审核中'
        break;    
        case 1: 
            n = '已通过'
        break;    
        case 2: 
            n = '审核不通过'
        break;
        default:
        break; 
    }
    return n
}
