/*
 * [page ajax]
 */
import qs from 'qs'
import axios from 'axios'
import { host } from '../config'
import $load from './loading'
import $toast from './toast'
import $bridge from './bridge'
import $store from '../store'

const service = axios.create({
    baseURL: host,
    timeout: 20000,
    validateStatus: x => x === 200
})
/**
 * 統一樣式的狀態碼處理
 * @param {any} h 囘調
 * @returns {null} 無
 */
const done = function(h){
    return this.then(h).catch(({ code, msg }) => {
        console.error('請求出錯啦 =>', code, msg)
    })
}

/**
 * 請求發送前處理
 * @param {any} o 配置前
 * @returns {object} 配置後
 */
const onsend = o => {
    const token = $store.state.userInfo.Data.Token
	if(o.url.indexOf("GetAllRecommandChannelGoods") != -1 || o.url.indexOf("GetAllRecommandSpecialistService") != -1|| o.url.indexOf("GetAllRecommandDoctorService") != -1|| o.url.indexOf("GetAllRecommandMallGoods") != -1|| o.url.indexOf("GetAllRecommendMenu") != -1 || o.url.indexOf("GetRecommondAmount") != -1){ 
		 $load.loadOut()
	}else{
		$load.loadIn()
	}
    if (typeof o.data === 'string'){
        o.headers[o.method]['Content-Type'] = 'application/json'
    }
    else if (Array.isArray(o.data)){
        const form = new FormData()

        Object.entries(o.data[0]).forEach(([ k, v ]) => {
            if (Array.isArray(v)){
                v.forEach(x => form.append(k, x))
            }
            else {
                form.append(k, v)
            }
        })

        o.data = form
    }
    else {
        o.data = qs.stringify(o.data)
    }
    if (token){
        o.headers.common.Authorization = token
    }
    return o
}

/**
 * 請求成功囘調
 * @param {object} o 請求對象
 * @returns {object} 響應
 */
const onsuccess = o => {
    //console.log('請求結果 =>', o.data)
    $load.loadOut()
    if (o.status !== 200){
        throw o.status
    }
    // 此處數據有後臺api數據格式決定
    const { Code, Message, Data } = o.data
	if(o.request.responseURL.indexOf('Prescription/Pharmacy/SetMyMedicationCompany') != -1){
		if(Code){
		    return o.data
		}
	}
	if(o.request.responseURL.indexOf('Prescription/SavePrescription') != -1 || o.request.responseURL.indexOf('Pharmacy/SwitchMyMedicationCompany') != -1){
		if(Code == 2){
		    return o.data
		}
    }
	if(o.request.responseURL.indexOf('prefRecommend/preAllSpecialty') != -1||o.request.responseURL.indexOf('prefRecommend/preSpecialty') != -1||o.request.responseURL.indexOf('prefRecommend/preSpecialtyList') != -1||o.request.responseURL.indexOf('prefRecommend/reDetailEdit') != -1){
		if(Code != 0){
		    return data
		}
	}
    if (Code === 0){
        return Data
    }
    else if(Code !== 0){
        if (Code == 401){
            //$bridge.callhandler('tokenInvalid')
            return $toast('登录过期，请重新登录！')
        }
        else{
        	if(o.request.responseURL.indexOf('SaveDoctorDuty') != -1){
        		//保存视频排班接口
		        $toast(Message)
		    }else{
		    	return $toast(Message)
		    }
        	//return $toast(Message)
            
        }
    }
    
    throw {
        Code,
        Message
    }
}

/**
 * 請求失敗囘調
 * @param {any} e 錯誤信息
 * @returns {any} 無
 */
const onerror = e => {
    $load.loadOut()
    const info = e.toString()
    const err = {
        Code: -1,
        Message: '请求失败'
    }
    if (info.includes('Network Error')){
        err.Message = '请求失败，请检查网络是否正常'
    }
    else if (info.includes('timeout of')){
        err.Message = '请求失败，请检查网络是否正常'
    }

    // // console.log('error =>', e)
    $toast(err.Message)
    throw err
}

Promise.prototype.done = done
window.Promise.prototype.done = done
service.interceptors.request.use(onsend)
service.interceptors.response.use(onsuccess, onerror)


export default {
    $get: (url, params) => service.get(url, { params }),
    $pop: (url, params) => service.delete(url, { params }),
    $put: (url, o) => service.put(url, o),
    $post: (url, o) => service.post(url, o),
    $patch: (url, o) => service.patch(url, o),
    $form: (url, o) => service.post(url, [o]),
    $auth: url => service.get(url, { responseType: 'blob' }).then(o => ({
        key: o.headers.key,
        data: o.data
    }))
}



